.main-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}
.main-container .mini-profile {
  position: absolute;
  left: 70%;
  top: 10.5%;
  border: 1px solid black;
  width: 20%;
  padding: 10px;
}
.main-container .mini-profile img {
  width: 25%;
}
.main-container .mini-profile2 {
  position: absolute;
  left: 70%;
  top: 50.5%;
  border: 1px solid black;
  width: 20%;
  padding: 10px;
}
.main-container .mini-profile2 img {
  width: 25%;
}
.compass{
  margin: 0px 100px 10px 0px;
  position: fixed;
  width: 15vh;
  height: 15vh;
  bottom: 0;
  right: 0;
  -webkit-animation: App-logo-spin 1.5s;
          animation: App-logo-spin 1.5s;
  z-index: 2;
}
.compass:hover{
  -webkit-transform: scale(1.1);
      -ms-transform: scale(1.1);
          transform: scale(1.1);
 
}
@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}
@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}


